import React, { useState } from "react";
import AboutMe from "./components/aboutMe";
import Projects from "./components/projects";

function App() {
  //counters
  const [aboutMe, setAboutMe] = useState("About-Me");
  const [myProjects, setMyProjects] = useState("Invisible");
  const changeTab = (tab) => {
    if (tab === 1) {
      setAboutMe("About-Me");
      setMyProjects("Invisible");
      return;
    }
    setAboutMe("Invisible");
    setMyProjects("Projects");
  };

  return (
    <div className="App">
      <div className="Top">
        <h1 className="Mega-Tittle">VonHoro</h1>
      </div>
      <div className="Tab-Manager">
        <div
          className={aboutMe === "About-Me" ? "Tab-selected" : "Tab"}
          onClick={(e) => changeTab(1)}
        >
          <h1 className="Tab-text">About me</h1>
        </div>
        <div
          className={aboutMe === "About-Me" ? "Tab" : "Tab-selected"}
          onClick={(e) => changeTab(2)}
        >
          <h1 className="Tab-text">Projects</h1>
        </div>
      </div>
      <AboutMe Class={aboutMe} />
      <Projects Class={myProjects} />
    </div>
  );
}
export default App;
