import ticCode2 from "../IMG/projects/Tic tac toe/Code2.png";
import rest1Main from "../IMG/projects/Restaurant-1/Main page.png";
import rest1Contact from "../IMG/projects/Restaurant-1/Contact.png";
import rest1Menu1 from "../IMG/projects/Restaurant-1/Menu Listing.png";
import rest1Menu2 from "../IMG/projects/Restaurant-1/Menu Listing 2.png";
import rest1Code from "../IMG/projects/Restaurant-1/Code.png";
import todoMain from "../IMG/projects/To do List/Main view.png";
import todoCreateEntry from "../IMG/projects/To do List/create entry.png";
import todoEntry from "../IMG/projects/To do List/Entry.png";
import todoEditEntry from "../IMG/projects/To do List/Edit entry.png";
import todoEditEntry2 from "../IMG/projects/To do List/Edit entry 2.png";
import todoEditedEntry from "../IMG/projects/To do List/Edited entry.png";
import rest2Main from "../IMG/projects/Restaurant-2/Main page.png";
import rest2Meals from "../IMG/projects/Restaurant-2/Main dishes menu.png";
import rest2Desserts from "../IMG/projects/Restaurant-2/Desset menu.png";
import rest2Drinks from "../IMG/projects/Restaurant-2/Drinks menu.png";
import rest2Order1 from "../IMG/projects/Restaurant-2/Place order.png";
import rest2Order2 from "../IMG/projects/Restaurant-2/Place order 2.png";
