import rest2Process from "../IMG/projects/Restaurant-2/Processing order.png";
import rest2CheckOut from "../IMG/projects/Restaurant-2/checkOut.png";
import rest2Receipt from "../IMG/projects/Restaurant-2/Receipt.png";
import rest2Code from "../IMG/projects/Restaurant-2/Code.png";
import weaMain from "../IMG/projects/Weather forecast/Start view.png";
import weaSearch from "../IMG/projects/Weather forecast/Search view.png";
import batMain from "../IMG/projects/Battleships/Original view.png";
import batChangePieces from "../IMG/projects/Battleships/Change amount of pieces.png";
import batDrag from "../IMG/projects/Battleships/Drag.png";
import batRotate from "../IMG/projects/Battleships/Rotate.png";
import batAllPieces from "../IMG/projects/Battleships/All pieces on board.png";
import batPlay from "../IMG/projects/Battleships/Game play.png";
import batOver from "../IMG/projects/Battleships/Game over.png";
import batCode1 from "../IMG/projects/Battleships/Code1.png";
import batCode2 from "../IMG/projects/Battleships/Code2.png";
import batCode3 from "../IMG/projects/Battleships/Code3.png";
import batCode4 from "../IMG/projects/Battleships/Code4.png";

const indexItems = [
  { name: "Sketch", uri: "sketch" },
  { name: "Calculator", uri: "calculator" },
  { name: "Library", uri: "Library" },
  { name: "Tic Tac Toe", uri: "Tic-Tac-Toe" },
  { name: "Restaurant - 1", uri: "Restaurant" },
  { name: "To do List", uri: "To-Do-List" },
  { name: "Restaurant - 2", uri: "Restaurant-with-React" },
  { name: "Weather Report", uri: "Weather-forecast" },
  { name: "Battleship", uri: "Battleships" },
];

function Projects(props) {
  const [skDetails, setSkDetails] = useState("Invisible");
  const [calDetails, setCalDetails] = useState("Invisible");
  const [libDetails, setLibDetails] = useState("Invisible");
  const [ticDetails, setTicDetails] = useState("Invisible");
  const [rest1Details, setRest1Details] = useState("Invisible");
  const [todoDetails, setTodoDetails] = useState("Invisible");
  const [rest2Details, setRest2Details] = useState("Invisible");
  const [weaDetails, setWeaDetails] = useState("Invisible");
  const [batDetails, setBatDetails] = useState("Invisible");
  const allClean = () => {
    setSkDetails("Invisible");
    setCalDetails("Invisible");
    setLibDetails("Invisible");
    setTicDetails("Invisible");
    setRest1Details("Invisible");
    setTodoDetails("Invisible");
    setRest2Details("Invisible");
    setWeaDetails("Invisible");
    setBatDetails("Invisible");
    return;
  };
  const setView = (e, item) => {
    if (e.target.parentElement.parentElement.className !== "Index") {
      allClean();
      const index = Array.from(
        document.getElementsByClassName("Index")[0].children
      );
      index.forEach((element, ind) => {
        index[ind].style.backgroundColor = "#fb7451";
        index[ind].firstChild.style.color = "#b82020";
        index[ind].lastChild.style.color = "#b82020";
      });
      return;
    }
    const index = Array.from(e.target.parentElement.parentElement.children);
    allClean();
    if (e.target.parentElement.style.backgroundColor === "rgb(148, 33, 4)") {
      e.target.parentElement.style.backgroundColor = "#fb7451";
      e.target.parentElement.firstChild.style.color = "#b82020";
      e.target.parentElement.lastChild.style.color = "#b82020";

      return;
    }

    index.forEach((element, ind) => {
      index[ind].style.backgroundColor = "#fb7451";
      index[ind].firstChild.style.color = "#b82020";
      index[ind].lastChild.style.color = "#b82020";
    });

    e.target.parentElement.style.backgroundColor = "#942104";
    e.target.parentElement.firstChild.style.color = "#ff7676";
    e.target.parentElement.lastChild.style.color = "#ff7676";
    switch (item) {
      case "Sketch":
        setSkDetails("Sk-Details");
        break;
      case "Calculator":
        setCalDetails("Cal-Details");
        break;
      case "Library":
        setLibDetails("Lib-Details");
        break;
      case "Tic Tac Toe":
        setTicDetails("Tic-Details");
        break;
      case "Restaurant - 1":
        setRest1Details("Rest1-Details");
        break;
      case "To do List":
        setTodoDetails("Todo-Details");
        break;
      case "Restaurant - 2":
        setRest2Details("Rest2-Details");
        break;

      case "Weather Report":
        setWeaDetails("Wea-Details");
        break;
      case "Battleship":
        setBatDetails("Bat-Details");
        break;
      default:
        break;
    }
  };

  return (
    <div className={props.Class}>
      <div className="Index">
        {indexItems.map((item, index) => (
          <div className="Index-items" Key={index + 1}>
            <h1>{item.name}</h1>
            <a
              href={`https://github.com/vonhoro/${item.uri}`}
              target="_blank"
              rel="noopener noreferrer"
            >
              Code
            </a>{" "}
            <br />
            <a
              href={`https://vonhoro.github.io/${item.uri}`}
              target="_blank"
              rel="noopener noreferrer"
            >
              View
            </a>
            <h2 onClick={(e) => setView(e, item.name)} className="Details" >Details</h2>
          </div>
        ))}
      </div>

      <div className={skDetails}>
        <p className="Project-text">
          The project is a "Canvas" where you draw something by moving your
          mouse over it.
        </p>
        <img alt="" src={sketchMain} className="Sketch-img" />
        <p className="Project-text">
          As you can see in the image above, it has 2 buttons, the top one gives
          you the ability to change the size of the canvas.
        </p>
        <img alt="" src={sketchSize} className="Sketch-img" />
        <p className="Project-text">
          {" "}
          The other button let you change the number of "pixels" or "squares"
          inside the canvas.
        </p>
        <img alt="" src={sketchPixel} className="Project-img" />
        <p className="Project-text">
          Because this was my first JavaScript project using the DOM, it was
          really challenging, especially when modifying the number of squares
          inside the canvas, because I had to delete the previous ones
          beforehand or it would had gone out of control. But after reading
          around the internet, I came with the following lines of codes, that
          were simple to implement.
        </p>
        <img alt="" src={sketchCode} className="Code" />
        <h2 onClick={(e) => setView(e, "Sketch")} className="Hide-text">
          Hide
        </h2>
      </div>

      <div className={calDetails}>
        <p className="Project-text">
          This one is a simple calculator, I had made a lot of them in the past
          but they were really simple because they only accepted 2 inputs at a
          time, this one can accept a lot more inputs and do the right
          calculation.
        </p>
        <img alt="" src={calculatorMain} className="Calculator-img" />
        <p className="Project-text">
          The calculator accepts inputs with key presses and clicks on the
          buttons, the code that gives it the ability to do
          multiplication/division first, then the other operations is the
          following.
        </p>
        <img alt="" src={calculatorCode} className="Code" />

        <p className="Project-text">
          This function is executed before any operation, at the time of
          execution it will know what happened beforehand and depending on the
          operator that is used it will either modify the current value of the
          accumulator "w" on multiplication/division and "(-)" the negative
          operator, or it will push the previous number to an array. At the end
          of the code, when pressing "=" all elements of the array are sum as
          shown below.
        </p>
        <img alt="" src={calculatorCode2} className="Code" />
        <p className="Project-text">
          As you may notice at the time I didn't know about the reduce() method
          on arrays.
        </p>

        <h2 onClick={(e) => setView(e, "Calculator")} className="Hide-text">
          Hide
        </h2>
      </div>

      <div className={libDetails}>
        <p className="Project-text">
          This project consist on adding the characteristics of books that you
          had read or plan to read, it gives you the ability to delete previous
          entries or to change their status, the main focus on the project was
          to learn about the local storage on JavaScript.
        </p>

        <img alt="" src={libraryMain} className="Project-img" />
        <p className="Project-text">
          By clicking on the Add a Book button, the following form will appear.
        </p>
        <img alt="" src={libraryForm} className="Project-img" />
        <p className="Project-text">
          {" "}
          After completing the form and clicking on add, the entry will be
          created.
        </p>
        <img alt="" src={libraryEntry1} className="Project-img" />
        <p className="Project-text">
          If you click on the entry, it will change its side, if you click on
          the delete button it will delete it.
        </p>
        <img alt="" src={libraryEntry2} className="Project-img" />
        <p className="Project-text">
          The code that let this happens is the following, it checks the Id of
          the clicked card, its status, deletes it, and then creates a new one
          with its status changed.
        </p>
        <img alt="" src={libraryCode} className="Code" />
        <p className="Project-text">
          If you leave some entries, and enter the web afterwards using the same
          explorer, you will see the same entries as the last time you visited
          (as long as you don't delete your cookies), this is possible by adding
          all the previous values to a string, then saving them in local storage
          as shown below.
        </p>
        <img alt="" src={libraryCode2} className="Code" />
        <p className="Project-text">
          To render all entries that are on local storage, at the start of the
          program, the following code gives you the way.{" "}
        </p>
        <img alt="" src={libraryCode3} className="Code" />
        <p className="Project-text">
          The code breaks the string into array entries, and for each entry, it
          creates a new card on the website. As a side note you may notice that
          in the GitHub code page there are one app.js and one appC.js file,
          they both do the same but the second one uses the class constructor
          syntax and the other uses the "standard" constructor. constructor
        </p>
        <h2 onClick={(e) => setView(e, "Library")} className="Hide-text">
          Hide
        </h2>
      </div>

      <div className={ticDetails}>
        <p className="Project-text">
          This is a simple 2 player Tic-tac-toe game, you input a name for each
          player and hit enter to play.
        </p>
        <img alt="" src={ticMain} className="Project-img" />
        <p className="Project-text">
          Once you hit play the game would be able to start.
        </p>
        <img alt="" src={ticGameStart} className="Project-img" />
        <p className="Project-text">
          For the colors and the players "pieces" I got inspiration on the manga
          series{" "}
          <a
            href="https://en.wikipedia.org/wiki/Kaguya-sama:_Love_Is_War"
            target="_blank"
            rel="noopener noreferrer"
          >
            {" "}
            Kaguya-sama: Love is War
          </a>{" "}
          as the pieces are 愛　that means love and 戦 that means war. Once a
          player wins, a message is displayed, and 1 point is assigned to them.
        </p>
        <img alt="" src={ticGameOver} className="Project-img" />
        <p className="Project-text">
          The objective of the project was to learn how to use factories, and
          they ended up as my preferred choice when building objects.
        </p>
        <img alt="" src={ticCode1} className="Code" />
        <p className="Project-text">
          The other code highlight would be on how to determine a winner, it
          does it by checking the board status after each movement and returns
          true if a win condition is met.
        </p>
        <img alt="" src={ticCode2} className="Code" />

        <h2 onClick={(e) => setView(e, "Tic Tac Toe")} className="Hide-text">
          Hide
        </h2>
      </div>

      <div className={rest1Details}>
        <p className="Project-text">
          This was a restaurant land page, the focus was on learning how to use
          webpack, and how to use and create tabs. Is important to say that none
          of the names, words, e-mails, or description are real, even if they
          may resemble reality.
        </p>
        <p className="Project-text">
          The main page only shows some pictures from restaurants and a vague
          description on this fake place (the pictures are taken from google).
        </p>
        <img alt="" src={rest1Main} className="Project-img" />
        <p className="Project-text">
          Just like above, the contact page is simple and has fake information.
        </p>
        <img alt="" src={rest1Contact} className="Project-img" />
        <p className="Project-text">
          The part where I gave more focus was on the Menu tab, there you can
          see the listed items, and by clicking them you will get their
          description.
        </p>
        <img alt="" src={rest1Menu1} className="Project-img" />
        <p className="Project-text">
          And you could check between 3 different listings.
        </p>
        <img alt="" src={rest1Menu2} className="Project-img" />
        <p className="Project-text">
          What made each item have a unique description is the following code,
          as they have almost the same Id it was easy to toggle between
          description and picture.
        </p>
        <img alt="" src={rest1Code} className="Code" />
        <h2 onClick={(e) => setView(e, "Restaurant - 1")} className="Hide-text">
          Hide
        </h2>
      </div>

      <div className={todoDetails}>
        <p className="Project-text">
          {" "}
          This project had a focus on forms, but of course it used all the
          techniques I learned, and giving the similarity on "concept" this was
          basically a Library 3.0(the difference is just that large!).
        </p>
        <img alt="" src={todoMain} className="Project-img" />
        <p className="Project-text">
          When you clicked the Add button, the form appears.
        </p>
        <img alt="" src={todoCreateEntry} className="Project-img" />
        <p className="Project-text">
          After filling the form and clicking on Add, the entry is created on
          its corresponding tab.
        </p>
        <img alt="" src={todoEntry} className="Project-img" />
        <p className="Project-text">
          If you click on the card, it will become bigger and you will be able
          to edits its values.
        </p>
        <img alt="" src={todoEditEntry} className="Project-img" />
        <img alt="" src={todoEditEntry2} className="Project-img" />
        <p className="Project-text">
          If you click on save, the entry will be edited, in the case you change
          its Importance, its color and location will change too!
        </p>
        <img alt="" src={todoEditedEntry} className="Project-img" />
        <p className="Project-text">
          There are too many things that I want to highlight on this code but
          saying that is the same as saying there is nothing to highlight, so
          you can check the code{" "}
          <a
            href="https://github.com/vonhoro/To-Do-List/tree/master/src"
            target="_blank"
            rel="noopener noreferrer"
          >
            here
          </a>
          . I must say that this also use local storage, so you can save your
          entries and check them every day(the deadline will update too if you
          set one).
        </p>
        <h2 onClick={(e) => setView(e, "To do List")} className="Hide-text">
          Hide
        </h2>
      </div>

      <div className={rest2Details}>
        <p className="Project-text">
          This was my first project using React, and it was a remake of my first
          Restaurant page, it has a lot of upgrades on the "menu" system, and I
          also added an order system.
        </p>
        <img alt="" src={rest2Main} className="Project-img" />
        <p className="Project-text">
          Looking at the menu, you can see that you have a more precise
          description, in both side you will see the menu items categories not
          displayed in the center.
        </p>
        <img alt="" src={rest2Meals} className="Project-img" />
        <p className="Project-text">
          If you click on Desserts, the following will happen.
        </p>
        <img alt="" src={rest2Desserts} className="Project-img" />
        <p className="Project-text">
          If you click on Drinks, this will happen.
        </p>

        <img alt="" src={rest2Drinks} className="Project-img" />

        <p className="Project-text">
          Going to the Order tab shows all items on the shop and a counter.
        </p>
        <img alt="" src={rest2Order1} className="Project-img" />
        <p className="Project-text">
          When you change the counter of an item, for each item, their amount
          and the total cost will be displayed on the middle bottom section.
        </p>
        <img alt="" src={rest2Order2} className="Project-img" />
        <p className="Project-text">
          After selecting more than 1 item, if you click on Confirm, a form will
          appear that the client will fill out with their personal information.
        </p>
        <img alt="" src={rest2Form} className="Project-img" />
        <p className="Project-text">
          Going forward you will see the order information, there you can go
          back and change stuff or click on check out.
        </p>
        <img alt="" src={rest2Process} className="Project-img" />
        <p className="Project-text">
          When you click on check out, depending on the paymeny you choosed, a
          different tab should open, but becaus this has no backend or bank
          account behind it, the following will appear.
        </p>
        <img alt="" src={rest2CheckOut} className="Project-img" />

        <p className="Project-text">
          Once you confirm the payment, the receipt will be displayed, and you
          will have the option to make a new order if you wish to.
        </p>
        <img alt="" src={rest2Receipt} className="Project-img" />
        <p className="Project-text">
          There is not too much code to highlight because this project tries to
          use all the important benefits that React brings, in this case using
          states. But one important thing is the backbone behind all the data,
          this comes from here.
        </p>
        <img alt="" src={rest2Code} className="Code" />
        <h2 onClick={(e) => setView(e, "Restaurant - 2")} className="Hide-text">
          Hide
        </h2>
      </div>

      <div className={weaDetails}>
        <p className="Project-text">
          The focus on this project was on learning how to handle API. When you
          open the site you only see this bar.
        </p>
        <img alt="" src={weaMain} className="Project-img" />
        <p className="Project-text">
          If you enter a valid value, the weather conditions on the place, and a
          gift representing the condition will appear.
        </p>
        <img alt="" src={weaSearch} className="Project-img" />
        <p className="Project-text">
          This was a simple project that I completed in few hours, most of the
          time was spent testing the APIs and checking how to retrieve the
          proper values.
        </p>
        <h2
          onClick={(e) => setView(e, "Weather Forecast")}
          className="Hide-text"
        >
          Hide
        </h2>
      </div>
      <div className={batDetails}>
        <h1>Battleship</h1>
        <p className="Project-text">
          This is a game inspired on the old classic, it works in the same
          manner, but you can use how many ships you want if their number is
          between 0 and 4!
        </p>
        <img alt="" src={batMain} className="Project-img" />
        <p className="Project-text">
          You can modify the number of pieces by changing their respective
          counter in the upper part.
        </p>
        <img alt="" src={batChangePieces} className="Project-img" />
        <p className="Project-text">
          Once you decided what pieces you want to use, hit accept, then you
          will be able to rotate the ships to drag them over.
        </p>
        <img alt="" src={batRotate} className="Project-img" />
        <p className="Project-text">
          Weather they are horizontal or vertical you can drag them and drop
          them on the board.{" "}
        </p>
        <img alt="" src={batDrag} className="Project-img" />
        <p className="Project-text">
          And you do that until all your pieces are on the board.
        </p>
        <img alt="" src={batAllPieces} className="Project-img" />{" "}
        <p className="Project-text">
          After placing everything you can hit the start game button and click
          the right board. Red squares are ships, the blue ones are water.
        </p>
        <img alt="" src={batPlay} className="Project-img" />
        <p className="Project-text">
          Each play you make will be answered by the CPU with an attack, until
          one of the 2 destroy the other fleet, once that happens this message
          will appear.
        </p>
        <img alt="" src={batOver} className="Project-img" />
        <p className="Project-text">
          This project has a lot behind the scenes, for example, to "place" the
          enemy pieces at random, the following algorithm is used.
        </p>
        <img alt="" src={batCode1} className="Code" />
        <p className="Project-text">
          It takes an array and the number of ships as inputs, then it decides
          by random if it will place the piece vertically or horizontally, the
          part above is the vertical case, it first checks the size of the ship,
          then the first square, then it checks if a previous square to use had
          been taken by the checkPrev array, after it checks all conditions it
          tries again, for vertical, or horizontal.
        </p>
        <img alt="" src={batCode2} className="Code" />
        <p className="Project-text">
          Above is the horizontal part, it is similar, the only difference is
          that it adds 1 instead of 10 for each square after the first. As you
          many noticed the shipPosition array is returned. The array would have
          "a12" as an entry in its 12 places if it is occupied by a ship, if it
          were not it would be "12" (the number).
        </p>
        <p className="Project-text">
          The position of the square is send by the second board
        </p>
        <img alt="" src={batCode3} className="Code" />
        <p className="Project-text">
          Then it checks if it is a number (water) or not (a ship), that is how
          the basic logic on the game works.
        </p>
        <img alt="" src={batCode4} className="Code" />
        <h2 onClick={(e) => setView(e, "Battleship")} className="Hide-text">
          Hide
        </h2>
      </div>
    </div>
  );
}
export default Projects;
